/* для элемента input c type="radio" */
.Register_customRadio__HDGEv>input {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

/* для элемента label связанного с .custom-radio */
.Register_customRadio__HDGEv>span {
    display: inline-flex;
    align-items: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

/* создание в label псевдоэлемента  before со следующими стилями */
.Register_customRadio__HDGEv>span::before {
    content: '';
    display: inline-block;
    width: 25px;
    height: 25px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #adb5bd;
    border-radius: 50%;
    margin-right: 0.5em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
}


/* стили для активной радиокнопки (при нажатии на неё) */
.Register_customRadio__HDGEv>input:not(:disabled):active+span::before {
    background-color: white;
    border:1px solid #474B4E;
}

/* стили для радиокнопки, находящейся в состоянии checked */
.Register_customRadio__HDGEv>input:checked+span::before {
    background-image: url("https://i.imgur.com/Xrdigpd.png");
}
